(function () {
    'use strict';
    // var jsonpatch = require('fast-json-patch');

    var journeyService = angular.module('journey.module').service('journeyService', ['$rootScope', '$q', '$filter', '$http', 'appState', 'CONFIG', 'UtilService', function ($rootScope, $q, $filter, $http, appState, CONFIG, UtilService) {
        var self = this,
            xmlhttp;

        self.init = function (url, callbackFunc) {
            if (window.XMLHttpRequest) { //For IE7+, Firefox, Chrome, Opera, Safari
                xmlhttp = new XMLHttpRequest();
            } else { //For IE6, IE5
                xmlhttp = new ActiveXObject("Microsoft.XMLHTTP");
            }
            xmlhttp.onreadystatechange = callbackFunc;
        };

        self.setDataInStorage = function (formDetails) {
            var arnData = appState.getApplicationData();
            arnData = angular.merge({}, arnData, formDetails);
            appState.setApplicationData(arnData);
        };

        self.getDataFromStorage = function () {
            return appState.getApplicationData();
        };

        self.getDataFromStorageContact = function () {
            return appState.getContactDetails();
        };

        self.getApplicantInfo = function (prop) {
            var localState = appState.getApplicationData();
            if (localState.primaryApplicant && localState.primaryApplicant[prop]) {
                return localState.primaryApplicant[prop];
            } else {
                return null;
            }
        };

        self.getSelectedCard = function () {
            var appData = appState.getApplicationData() || {};
            return appData.primaryApplicant ? appData.primaryApplicant.card : null;
        };
        
        self.getPerfiosSkip = function () {
            var appData = appState.getApplicationData() || {};
            return appData.primaryApplicant ? appData.primaryApplicant.perfiosSkip : null;
        };

        self.isIncomeDocRequired = function () {
            var localState = appState.getApplicationData(), incomeDocRequired = false;
            if (localState && localState.primaryApplicant && localState.primaryApplicant.employmentDetails) {
                var hasChangedCompanyName = localState.primaryApplicant.employmentDetails.changedCompanyName ? localState.primaryApplicant.employmentDetails.changedCompanyName : 'N';
                var skipPerfios = localState.primaryApplicant.hasSkippedPerfios ? localState.primaryApplicant.hasSkippedPerfios : 'N';
                var panStatusFlag = localState.primaryApplicant.panStatusFlag ? localState.primaryApplicant.panStatusFlag : 'N';
                if (hasChangedCompanyName == 'Y' || skipPerfios == 'Y' || panStatusFlag == 'NO_PAN_FOUND' || localState.primaryApplicant.employmentDetails.isUnhapppyJourney || localState.primaryApplicant.employmentDetails.isAnnualIncomeDisabled === 'N') {
                    incomeDocRequired = true;
                } else {
                    incomeDocRequired = false;
                }
            }
            return incomeDocRequired;
        };

        self.hasIncomeDoc = function () {
            var localState = appState.getApplicationData(), hasDoc = false;
            if (localState && localState.primaryApplicant && localState.primaryApplicant.documents) {
                var incomeDocs = localState.primaryApplicant.documents.incomeProof || [];
                if (angular.isArray(incomeDocs) && incomeDocs.length > 0) {
                    hasDoc = true;
                }
            }
            return hasDoc;
        };

        self.setApplicationInStorage = function (application) {
            appState.setApplicationData(application);
        };

        self.browserClose = function (view) {
            if (!UtilService.isEmpty(view)) {
                var arn = self.getDataFromStorage().primaryApplicant.arn;
                var apiURL, async = false;
                apiURL = CONFIG.apiUrl + '/iccm/sendnotification/' + arn + '/' + view;
                if (window.XMLHttpRequest) { //For IE7+, Firefox, Chrome, Opera, Safari
                    xmlhttp = new XMLHttpRequest();
                    xmlhttp.open("GET", apiURL, async);
                    xmlhttp.send();
                }
            }
        };

        self.getCountries = function () {
            return $http.get('resources/data/countries.json', {
                cache: true
            }).then(function (response) {
                return response.data;
            });
        };

        self.getErrorMessages = function () {
            return $http.get('resources/data/errorMessages.json', {
                cache: true
            }).then(function (response) {
                return response.data;
            });
        };

        self.getBanks = function () {
            return $http.get('resources/data/banks.json', {
                cache: true
            }).then(function (response) {
                return response.data;
            });
        };

        self.getAddressByPostalCode = function (postalCode, maskContainer) {
            if ($rootScope.integrationsActive) {
                return $http.get(CONFIG.apiUrl + '/lookup/pincode/' + postalCode, {}).then(function (response) {
                    if (response && response.status === 200) {
                        return response.data;
                    }
                });
            } else {
                var def = $q.defer();
                if (postalCode === "221204") {
                    def.resolve({ city: 'Varanasi', district: null, state: 'Uttar Pradesh' });
                } else if (postalCode === '201301') {
                    def.resolve({ city: 'Noida', district: 'Noida', state: 'Uttar Pradesh' });
                } else if (postalCode === '110096') {
                    def.resolve({ city: 'New Ashok Nagar', district: 'East Delhi', state: 'Delhi' });
                } else {
                    def.resolve({ city: '', district: '', state: '' });
                }
                return def.promise;
            }
        };
        
        self.setRedactedAadhaar = function (RedactedAadhaar) { 
            return appState.setRedactedAadhaar(RedactedAadhaar)
        };
        self.setAapsIssue = function (AapsIssue) {
            return appState.setAapsIssue(AapsIssue)
        };

        self.setOtpIssue = function (OtpIssue) {
            return appState.setOtpIssue(OtpIssue)
        };

        self.getRedactedAadhaar = appState.getRedactedAadhaar;
        self.getAapsIssue = appState.getAapsIssue;
        self.getOtpIssue = appState.getOtpIssue;
        self.isStaffJourney = appState.isStaffJourney;
        self.setDisableBrowserClose = appState.setDisableBrowserClose;
        self.isBrowserCloseDisabled = appState.isBrowserCloseDisabled;
    }]);

    module.exports = journeyService.name;
})();


