(function () {
    'use strict';
    var angular = require('angular');
    require('./overlayConfirmation.modal.component.scss')

    var modalController = function (analyticService) {
        var vm = this;

        vm.$onInit = function () {
            analyticService.buttonList.popup.event_content = "save for later popup";
            analyticService.trackUserAction(analyticService.buttonList.popup);
        };

        /**
         * This function is triggered when we select or unselect card.
         * We push the selected card in selectedPrimaryCards array and
         *  pop the unselected card.
         */
        vm.Close = function (isSelected) {
            analyticService.buttonList.buttonClick.event_content = "yes";
            analyticService.trackUserAction(analyticService.buttonList.buttonClick);
            vm.close({
                $value: isSelected
            });
        };

        vm.cancel = function () {
            analyticService.buttonList.buttonClick.event_content = "no";
            analyticService.trackUserAction(analyticService.buttonList.buttonClick);
            vm.dismiss({
                $value: 'cancel'
            });
        };
    };

    var modalComp = angular.module('journey.module').component('overlaySaveConfirmation', {
        template: require('./overlayConfirmation.modal.component.html'),
        bindings: {
            resolve: '<',
            close: '&',
            dismiss: '&'
        },
        controller: ['analyticService', modalController]
    });
    module.exports = modalComp.name;
})();



