(function () {
    'use strict';
    var angular = require('angular');
    require('./journey.component.scss');
    // require('./select.css');
    var journeyComp = angular.module('journey.module').component('journey', {
        template: require('./journey.component.html'),
        controller: ['$rootScope', '$scope', '$state', '$timeout', '$transitions', 'journeyService', 'UtilService', function ($rootScope, $scope, $state, $timeout, $transitions, journeyService, UtilService) {
            var vm = this;
            vm.hideJourneyHeader = false;
            vm.$onInit = function () {
                /* if ($state.current.name === 'app.journey.completion') {
                    vm.hideHeader = true;
                } else {
                    vm.hideHeader = false;
                }; */
                window.onpopstate = function (e) {
                    window.history.forward(1);
                }
                window.onkeydown = function (e) {
                    if (!UtilService.isEmpty(e.target.className) && e.target.className.indexOf('ui-select-toggle') > -1 && (e.keyCode === 32 || e.keyCode === 13)) {
                        e.preventDefault();
                    } else if (!UtilService.isEmpty(e.target.className) && e.target.className.indexOf('ui-select-search') > -1 && (e.keyCode === 38 || e.keyCode === 40)) {
                        e.preventDefault();
                    } else {
                        return true;
                    }
                };

                var isbeforeUnload = false;
                window.addEventListener("beforeunload", function (e) {
                    isbeforeUnload = true;
                    handleBrowserClose();
                    return "Sure?";
                });
                // In mobile browsers "beforeunload" doesn't trigger
                window.addEventListener("unload", function (e) {
                    if (isbeforeUnload) return;
                    handleBrowserClose();
                    return "Sure?";
                });
                showSelectedCard();
                setBannerTop();
            };

            $scope.$on('card-selected', function () {
                showSelectedCard();
            });

            var showSelectedCard = function () {
                var localState = journeyService.getDataFromStorage();
                if (localState && localState.primaryApplicant && localState.primaryApplicant.card) {
                    vm.selectedCard = localState.primaryApplicant.card;
                    if (vm.selectedCard.key === 'VPC') {
                            vm.selectedCard.offers = [
                                { title: 'NIL', description: 'Joining & Annual Fees' },
                                { title: 'Complete your Credit Card Application in just 3 steps', description: 'Apply for card -> Get Instant Approval -> Complete your video KYC = Win INR 250 Amazon e-gift voucher' }
                            ]
                        } else if (vm.selectedCard.key === 'SVC') {
                            vm.selectedCard.offers = [
                                { title: 'NIL', description: 'Joining fees' },
                                { title: 'Complete your Credit Card Application in just 3 steps', description: 'Apply for card -> Get Instant Approval -> Complete your video KYC = Win INR 250 Amazon e-gift voucher' }
                            ]
                        } else if (vm.selectedCard.key === 'CBC') {
                            vm.selectedCard.offers = [
                                // { title: 'NIL', description: 'Joining fees' }, //commented as asmita requiremet @07-09-2023
                                { title: '', description: '' },
                                { title: 'Complete your Credit Card Application in just 3 steps', description: 'Apply for card -> Get Instant Approval -> Complete your video KYC = Win INR 250 Amazon e-gift voucher' }
                            ]
                        } else if (vm.selectedCard.key === 'HPR') {
                            vm.selectedCard.offers = [
                                { title: '12000', description: 'Joining fees' },
                                { title: 'Complete your Credit Card Application in just 2 steps', description: 'Apply for card -> Get Instant Approval ' }
                            ]
                        }else if (vm.selectedCard.key === 'MJY') {
                            vm.selectedCard.offers = [
                                { title: '', description: '' },
                                { title: 'Complete your Credit Card Application in just 3 steps', description: 'Apply for card -> Get Instant Approval -> Complete your video KYC = Win INR 250 Amazon e-gift voucher' }
                            ]
                        }
                }
            };

            var setBannerTop = function () {
                $timeout(function () {
                    var header = angular.element(document.querySelector('header'))[0]
                    vm.topMargin = header.offsetHeight;
                    var progBar = angular.element(document.querySelector('.step-header'))[0]
                    if (progBar) {
                        vm.topMargin += progBar.offsetHeight;
                    }
                });
            };

            var handleBrowserClose = function () {
                var view;
                switch ($state.current.name) {
                    case 'app.journey.s3-personalDetails':
                        view = 0;
                        break;
                    // case 'app.journey.s4-personalDemographic':
                    //     view = 0;
                    //     break;
                    case 'app.journey.s5-employmentDetails':
                        view = 0;
                        break;
                    case 'app.journey.s6-incomeDetails':
                        view = 0;
                        break;
                    case 'app.journey.s7-review':
                        view = 0;
                        break;
                    case 'app.journey.s8-consent':
                        view = 0;
                        break;
                    case 'app.journey.card-DIP':
                        view = 0;
                        break;
                    case 'app.journey.balance-transfer':
                        view = 4;
                        break;
                    case 'app.journey.supplementary':
                        view = 5;
                        break;
                    case 'app.journey.document':
                        view = 0;
                        break;
                    case 'app.journey.signature':
                        view = 0;
                        break;
                        defaut:
                        break;
                };
                if (!UtilService.isEmpty(view)) {
                    var isThirdParty = journeyService.isBrowserCloseDisabled();
                    if (!isThirdParty) {
                        // console.log("----Browser close event: Triggered----");
                        journeyService.browserClose(view);
                    } else {
                        // console.log("----Browser close event: Not Triggered----");
                        journeyService.setDisableBrowserClose(false);
                    }
                }
            };

            $transitions.onSuccess({}, function (transition) {
                setBannerTop();
            });

            $rootScope.$on('trigger-verification-loader', function (event, args) {
                vm.hideJourneyHeader = args.showLoader;
                $rootScope.showSelectedCard = args.showCard;
            });

            $rootScope.$on('hide-step-header', function (event, args) {
                vm.hideJourneyHeader = args.hideHeader;
            });
        }]
    }).directive('focusOnError', function () {
        return {
            restrict: 'A',
            link: function (scope, elem) {

                // set up event handler on the form element
                var continueBtn = angular.element(document.getElementById('continue-btn'));
                continueBtn.on('click', function () {

                    // find the first invalid element
                    var firstInvalid = elem[0].querySelector('.ng-invalid');

                    // if we find one, set focus
                    if (firstInvalid) {
                        firstInvalid.focus();
                    }
                });
            }
        };
    }).directive("preventTypingGreater", function () {
        return {
            link: function (scope, element, attributes) {
                var oldVal = null;
                element.on("keydown keyup", function (e) {
                    if (Number(element.val()) > Number(attributes.max) &&
                        e.keyCode != 46 // delete
                        &&
                        e.keyCode != 8 // backspace
                    ) {
                        e.preventDefault();
                        element.val(oldVal);
                    } else {
                        oldVal = Number(element.val());
                    }

                });
            }
        }
    });
    module.exports = journeyComp.name;
})();



